<template>
  <div class="cart">
    <router-link :to="{name: 'Home'}">Главная</router-link>
    <div class="cart__main-wrap">
      <!-- Заголовок -->
      <div class="cart__header">
        <h2 v-if="cart_data.length">Корзина <span class="cart__quantity-header">({{cart_data.length}} шт)</span></h2>
        <h2 v-else>Корзина пуста</h2>
      </div>
      <!-- Содержимое -->
      <div class="cart__products">
        <div class="cart__left-side">
          <CartItem
            v-for="(item, index) in cart_data"
            :key="item.id"
            :cart_data_item="item"
            @deleteFromCart="deleteFromCart(index)"
            @increment="increment(index)"
            @decrement="decrement(index)"
          ></CartItem>
        </div>
        <div class="cart__right-side">
          <div class="cart__order">
            <button class="cart__btn-order">Перейти к оформлению</button>
            <span>Доступные способы и время доставки можно выбрать при оформлении заказа</span>
          </div>
          <div class="cart__final-product">
            <div class="cart__total-basket">
              <p>Ваша корзина</p>
              <p>{{cart_data.length}}</p>
            </div>
            <div class="cart__total-cost">
              <p>Общая стоимость</p>
              <p>{{cartTotalCost}} р.</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  
</template>

<script>
  import CartItem from '../components/Cart-item'
  import {mapActions} from 'vuex'

  export default {
    name: 'Cart',
    components: {
      CartItem
    },
    props: {
      cart_data: {
        type: Array,
        default() {
          return []
        },
      }
    },
    methods: {
      ...mapActions(['DELETE_FROM_CART', 'INCREMENT_CART_ITEM', 'DECREMENT_CART_ITEM']),
      deleteFromCart(index) {
        this.DELETE_FROM_CART(index)
      },
      increment(index) {
        this.INCREMENT_CART_ITEM(index)
      },
      decrement(index){
        this.DECREMENT_CART_ITEM(index)        
      }
    },
    computed: {
      cartTotalCost() {
        let result = []

        if(this.cart_data.length) {
          for(let item of this.cart_data) {
            result.push(item.price * item.quantity)
          }
  
          result = result.reduce(function (sum, el) {
            return sum + el;
          })

          return result 
        } else {
          return 0
        }
      }
    }
  }
</script>

<style lang="scss">
  .cart {
    
    max-width: 1100px;
    width: 75%;
    margin: 20px auto;
    &__main-wrap {
    }
    // Заголовок
    &__header {
      font-family: 'Gabriela', serif;
      h2 {
        font-size: 36px;
        font-weight: normal;
      }
      .cart__quantity-header {
        font-size: 24px;
        font-weight: normal;
        color: #BDBDBD;
      }
    }
    // Содержимое
    &__products {
      display: flex;
    }
    &__left-side {
      width: 60%;
    }
    &__right-side {
      margin: 0 50px;
      font-family: "PT Sans";
    }
    &__order {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0 0 20px;
    }
    .cart__btn-order {
      font-weight: bold;
      border: none;
      background: #FBD765;
      width: 230px;
      height: 62px;
      margin-bottom: 20px;
    }
    &__order span {
      text-align: left;
      font-size: 12px;
      color: #BDBDBD;
    }
    &__total-basket {
      display: flex;
      justify-content: space-between;
      border-top: 2px solid #FBD765;
      padding: 10px 10px;
    }
    &__total-cost {
      display: flex;
      justify-content: space-between;
      border-top: 2px solid #FBD765;
      padding: 10px 10px;
    }
    @media (max-width: 1100px) {
      .cart {
        &__products {
          flex-direction: column;
          align-items: center;
        }
        &__left-side {
          width: 100%;
          margin: 0 0 50px;
        }
        &__right-side {
          display: flex;
          flex-direction: column;
          width: 400px;
        }
      }
    }
    @media (max-width: 599px) {
      .cart {
        &__left-side {
          width: 120%;
        }
        &__right-side {
          width: 100%;

        }
      }
    }
  }
</style>