<template>
  <div class="cart-item">
    <img class="cart-item__image" :src="require('../app/images/' + cart_data_item.image)" alt="">
    <div class="cart-item__title">{{cart_data_item.title}}</div>
    <div class="cart-item__quantity">
      <button class="cart-item__btn btn-minus" @click="decrementItem"></button>
      <span>{{cart_data_item.quantity}}</span>
      <button class="cart-item__btn btn-plus" @click="incrementItem"></button>
    </div>
    <div class="cart-item__price">{{cart_data_item.price}} р.</div>
    <button class="cart-item__btn btn-delete" @click="deleteFromCart"></button>
  </div>
</template>

<script>
  export default {
    name: 'CartItem',
    props: {
      cart_data_item: {
        type: Object,
        default() {
          return []
        }
      }
    },
    methods: {
      incrementItem(){
        this.$emit('increment')
      },
      decrementItem() {
        this.$emit('decrement')
      },
      deleteFromCart(){
        this.$emit('deleteFromCart')
      }
    },
    // mounted() {
    //    this.$set(this.cart_data_item, 'quantity', 1) 
    // }
  }
</script>

<style lang="scss">
  .cart-item:last-child {
    border-bottom: 2px solid #FBD765;
  }
  .cart-item{
    font-size: 14px;
    display: flex;
    font-family: "PT Sans";
    align-items: center;
    justify-content: space-between;
    border-top: 2px solid #FBD765;
    padding: 0 15px 0 5px;
    &__header {
      display: flex;
      align-items: center;
    }
    &__image {
      width: 100px;
    }
    &__title {
      width: 140px;
    }
    &__quantity {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 120px;
    }
    &__price {
      width: 60px;
      text-align: center;
    }
    // Стилищзация кнопок
    &__btn {
      border: none;
      width: 16px;
      height: 16px;
    }
    &__btn.btn-minus {
      background: url("../app/icons/minusbutton.svg") no-repeat center;
      // background-size: 16px;
    }
    &__btn.btn-plus {
      background: url("../app/icons/plusbutton.svg") no-repeat center;
    }
    &__btn.btn-delete {
      background: url("../app/icons/deletebutton.svg") no-repeat center;
    }
  }
   @media (max-width: 599px) {
    .cart-item {
      padding: 20px 0;
      &__image {
        display: none;
      }
      &__title {
        width: 120px;
      }
      &__quantity {
        width: 60px;
      }
    }
   }
</style>